import React from 'react';
import {
    Row,
    Col,
    Card,
    CardDeck,
    Button
} from 'react-bootstrap';

import styles from './WorkTermReport.module.css';

function WorkTermReport(props) {

    return (
        <div className={styles.wrapper}>
            <h1>{props.jobTitle} at {props.company}</h1>
            <h3>About The Company</h3>
            <p>{props.aboutCompany}</p>
            {props.stats && (
                <CardDeck className={styles.statsCardDeck}>
                    {props.stats.map(stat => (
                        <Card body>
                            <h4>{stat.value}</h4>
                            <p>{stat.text}</p>
                        </Card>
                    ))}
                </CardDeck>
            )}
            <Row>
                <Col>
                    <img className={styles.map} alt={`Map showing ${props.company}`} src={props.map} />
                </Col>
                <Col className={styles.verticalAlign}>
                    <div>
                        <h4>{props.company}</h4>
                        <p>{props.companyAddress}</p>
                        <p>Phone: {props.companyPhone}</p>
                        {props.companyWebsite && (
                            <a href={props.companyWebsite}>
                                <Button variant='outline-secondary'>Visit Website</Button>
                            </a>
                        )}
                    </div>
                </Col>
            </Row>
            <h3>Job Description</h3>
            <Row>
                <Col className={styles.verticalAlign}>
                    <p className={styles.jobDescription}>{props.jobDescription}</p>
                </Col>
                <Col>
                    <img alt='' src={props.jobDescriptionImg} />
                </Col>
            </Row>
            <h3>Goals For This Work Term</h3>
            <Row>
                {props.goals.map(goal => (
                    <Col className={styles.goalsCol}>
                        <img alt='' src={goal.image} />
                        <h5>{goal.title}</h5>
                        <p>{goal.text}</p>
                    </Col>
                ))}
            </Row>
            <h3>Reflection</h3>
            <p>{props.reflection}</p>
            <h3>Conclusion</h3>
            <p>{props.conclusion}</p>
            <h3>Acknowledgments</h3>
            {props.acknowledgments && (
                <CardDeck className={styles.acknowledgmentsCardDeck}>
                    {props.acknowledgments.map(acknowledgment => (
                        <Card body>
                            <h6>{acknowledgment.name}</h6>
                            <p>{acknowledgment.position}</p>
                        </Card>
                    ))}
                </CardDeck>
            )}
        </div>
    )

}

export default WorkTermReport;